import { createApp } from 'vue';
import 'bootstrap';
import VCalendar from 'v-calendar';
import 'v-calendar/style.css';
import mitt from 'mitt';
import { createI18n } from 'vue-i18n';
// 本地語言
import zh from '@/assets/languageText/zh-TW.json';
import en from '@/assets/languageText/en-US.json';
import id from '@/assets/languageText/id-ID.json';
import vi from '@/assets/languageText/vi-VN.json';
import toCurrency from './assets/javascript/toCurrency';
import App from './App.vue';
import router from './router';

const i18n = createI18n({
  legacy: false,
  // eslint-disable-next-line func-names
  locale: (function () {
    if (localStorage.getItem('lang')) {
      return localStorage.getItem('lang');
    }
    return 'en-US';
  }()),
  // locale: 'en-US',
  fallbackLocale: 'en-US',
  messages: {
    'zh-TW': zh,
    'en-US': en,
    'id-ID': id,
    'vi-VN': vi,
  },
});

const emitter = mitt();

const app = createApp(App);

app.config.globalProperties.$toCurrency = toCurrency;
app.config.globalProperties.$emitter = emitter;

app.use(i18n);
app.use(VCalendar, {});
app.use(router);

// 切換網頁時回到最上方
router.afterEach(() => {
  window.scrollTo(0, 0);
});

app.mount('#app');
