import { createRouter, createWebHashHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    children: [
      {
        path: '/',
        name: 'index',
        component: () => import('../views/front/IndexView.vue'),
      },
      {
        path: 'about',
        name: '關於',
        component: () => import('../views/front/AboutView.vue'),
      },
      {
        path: 'shop',
        name: '商店',
        component: () => import('../views/front/ShopView.vue'),
        children: [
          {
            path: 'shopping',
            name: '購物',
            component: () => import('../views/front/shop/ShoppingView.vue'),
          },
          {
            path: 'payment',
            name: '付款',
            component: () => import('../views/front/shop/PaymentView.vue'),
          },
        ],
      },
      {
        path: 'order',
        name: '訂單',
        component: () => import('../views/front/OrderView.vue'),
        children: [
          {
            path: 'prepayment',
            name: '代付款',
            component: () => import('../views/front/order/PrepaymentView.vue'),
          },
          {
            path: 'completPay',
            name: '付款完成',
            component: () => import('../views/front/order/CompletView.vue'),
          },
          {
            path: 'completOrder',
            name: '訂單完成',
            component: () => import('../views/front/order/OrderCompletView.vue'),
          },
          {
            path: 'dining',
            name: '出餐狀態',
            component: () => import('../views/front/order/DiningOutView.vue'),
          },
          {
            path: 'invalid',
            name: '不成立',
            component: () => import('../views/front/order/InvalidView.vue'),
          },
        ],
      },
    ],
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../views/AboutView.vue'),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
